import { Heart, Shield, Gift } from "lucide-react";

const features = [
  {
    icon: Heart,
    title: "Yu Thng V iu Kin",
    description: "Mi ng gp ca v l mt li yu thng gi n, gp phn xy dng t m hnh phc.",
    color: "text-primary",
    bgColor: "bg-rose-light",
  },
  {
    icon: Shield,
    title: "Minh Bch Ti Chnh",
    description: "Cam oan 100% s tin c chng s dng minh bch. Bo co chi tit nh k n v.",
    color: "text-primary",
    bgColor: "bg-rose-light",
  },
  {
    icon: Gift,
    title: "c Quyn c Bit",
    description: "Nhn li cm n nng m, thip chc Tt vit tay v nhiu bt ng t chng!",
    color: "text-primary",
    bgColor: "bg-rose-light",
  },
];

const AboutSection = () => {
  return (
    <section className="py-20 px-4 bg-rose-light/50">
      <div className="max-w-6xl mx-auto">
        {/* Heading */}
        <div className="text-center mb-16">
          <h2 className="heading-display text-4xl md:text-5xl mb-6">
            <span className="text-foreground">V </span>
            <span className="text-primary">D n </span>
            <span className="text-accent">Nui Chng</span>
          </h2>
          <p className="text-muted-foreground text-lg max-w-2xl mx-auto">
            Mt d n c thnh lp vi tnh yu thng, s minh bch v cam kt mang n hnh phc cho gia nh nh.
          </p>
        </div>

        {/* Feature Cards */}
        <div className="grid md:grid-cols-3 gap-6">
          {features.map((feature, index) => (
            <div 
              key={index}
              className="card-soft p-8 text-center"
            >
              <div className={`icon-circle ${feature.bgColor} mx-auto mb-6`}>
                <feature.icon className={`w-6 h-6 ${feature.color}`} />
              </div>
              <h3 className="font-display text-xl font-semibold text-foreground mb-4">
                {feature.title}
              </h3>
              <p className="text-muted-foreground leading-relaxed">
                {feature.description}
              </p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default AboutSection;
