import { Heart } from "lucide-react";

const FloatingHearts = () => {
  return (
    <>
      {/* Top left */}
      <Heart 
        className="absolute top-20 left-16 w-6 h-6 text-rose-medium animate-float opacity-60" 
        strokeWidth={1.5}
      />
      
      {/* Top right */}
      <Heart 
        className="absolute top-32 right-20 w-5 h-5 text-rose-medium animate-float-delayed opacity-50" 
        strokeWidth={1.5}
      />
      
      {/* Middle left */}
      <Heart 
        className="absolute top-1/2 left-24 w-4 h-4 text-rose-medium animate-float opacity-40" 
        strokeWidth={1.5}
      />
      
      {/* Middle right */}
      <Heart 
        className="absolute top-2/3 right-28 w-6 h-6 text-rose-medium animate-float-delayed opacity-50" 
        strokeWidth={1.5}
      />

      {/* Sparkles */}
      <span className="absolute top-28 left-1/3 text-orange opacity-60 animate-pulse-slow"></span>
      <span className="absolute top-1/2 right-1/4 text-orange opacity-50 animate-pulse-slow" style={{ animationDelay: "1s" }}></span>
      <span className="absolute bottom-1/4 left-1/4 text-rose-medium opacity-40 animate-pulse-slow" style={{ animationDelay: "2s" }}></span>
    </>
  );
};

export default FloatingHearts;
