import { useState } from "react";
import { Heart, Check } from "lucide-react";

const CTASection = () => {
  const [agreed, setAgreed] = useState(false);

  const handleAgree = () => {
    setAgreed(true);
  };

  return (
    <section className="py-20 px-4">
      <div className="max-w-lg mx-auto">
        <div className="card-soft p-10 text-center">
          {/* Heart Icon */}
          <div className="w-16 h-16 bg-primary rounded-full flex items-center justify-center mx-auto mb-6">
            <Heart className="w-8 h-8 text-primary-foreground fill-current" />
          </div>

          {/* Title */}
          <h2 className="font-display text-2xl md:text-3xl font-semibold text-foreground mb-4">
            ng K Tham Gia
          </h2>
          
          <p className="text-muted-foreground mb-6">
            V c ng  tham gia d n Nui Chng khng?
          </p>

          {!agreed ? (
            <>
              <p className="text-sm text-muted-foreground mb-8 leading-relaxed">
                Bng vic nhn nt bn di, v ng  ng gp hng thng  nui chng v nhn cc c quyn tng ng.
              </p>

              {/* CTA Button */}
              <button 
                onClick={handleAgree}
                className="btn-rose text-lg mb-4"
              >
                <Heart className="w-5 h-5 fill-current" />
                ng  Nui Chng
              </button>

              <p className="text-xs text-muted-foreground">
                * Quyt nh sng sut nht i v
              </p>
            </>
          ) : (
            <div className="py-6">
              <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Check className="w-6 h-6 text-green-600" />
              </div>
              <h3 className="font-display text-xl font-semibold text-foreground mb-2">
                V Tuyt Vi!
              </h3>
              <p className="text-muted-foreground mb-4">
                Cm n v  ng  tham gia d n Nui Chng.
                <br />
                Chng yu v nhiu lm!
              </p>
              <div className="text-3xl animate-pulse-heart"></div>
            </div>
          )}
        </div>
      </div>
    </section>
  );
};

export default CTASection;
