import { Heart, Star, Crown, Check } from "lucide-react";

const plans = [
  {
    icon: Heart,
    name: "V Tt",
    subtitle: "Gp mt cht tnh cm mi thng",
    price: "1 Triu",
    period: "/thng",
    features: [
      "Li cm n hng thng",
      "Li chc tnh cm",
      "Ghi danh v yu",
    ],
    popular: false,
    iconBg: "bg-rose-light",
    iconColor: "text-primary",
  },
  {
    icon: Star,
    name: "V ng Yu",
    subtitle: "Gp sc ng k cho hnh phc gia nh",
    price: "3 Triu",
    period: "/thng",
    features: [
      "Tt c quyn li V Tt",
      "Thip chc Tt vit tay",
      "Cp nht bo co chi tiu",
      "Qu bt ng dp c bit",
    ],
    popular: true,
    iconBg: "bg-rose-light",
    iconColor: "text-primary",
  },
  {
    icon: Crown,
    name: "V Hon Ho",
    subtitle: "V VIP ca d n Nui Chng",
    price: "5 Triu",
    period: "/thng",
    features: [
      "Tt c quyn li V ng Yu",
      "V VIP c quyn",
      "Video cm n c nhn ha",
      "u tin c tm s cng chng",
      "Qu Tt c bit",
    ],
    popular: false,
    iconBg: "bg-crown-gold/20",
    iconColor: "text-crown-gold",
  },
];

const PricingSection = () => {
  return (
    <section className="py-20 px-4 bg-rose-light/50">
      <div className="max-w-5xl mx-auto">
        {/* Heading */}
        <div className="text-center mb-16">
          <h2 className="heading-display text-4xl md:text-5xl mb-6">
            <span className="text-foreground">Chn Mc </span>
            <span className="text-accent">Nui Chng</span>
          </h2>
          <p className="text-muted-foreground text-lg max-w-2xl mx-auto">
            Chn mc ph hp vi ti chnh ca v. Mi ng gp u qu gi!
          </p>
        </div>

        {/* Pricing Cards */}
        <div className="grid md:grid-cols-3 gap-6 items-center">
          {plans.map((plan, index) => (
            <div 
              key={index}
              className={`bg-card rounded-2xl flex flex-col relative transition-all duration-300 hover:-translate-y-2 hover:shadow-xl h-full ${
                plan.popular 
                  ? 'shadow-xl ring-2 ring-primary z-10 p-10 md:scale-[1.08] md:-my-4' 
                  : 'shadow-md hover:shadow-lg p-8'
              }`}
            >
              {/* Popular Badge */}
              {plan.popular && (
                <div className="absolute -top-3 left-1/2 -translate-x-1/2 bg-primary text-primary-foreground text-xs font-medium px-4 py-1.5 rounded-full">
                  Ph Bin
                </div>
              )}

              {/* Icon */}
              <div className={`w-12 h-12 rounded-xl flex items-center justify-center mx-auto mb-4 ${plan.iconBg}`}>
                <plan.icon className={`w-5 h-5 ${plan.iconColor}`} />
              </div>

              {/* Plan Name */}
              <h3 className="font-display text-xl font-semibold text-center text-foreground mb-1">
                {plan.name}
              </h3>
              <p className="text-sm text-center text-muted-foreground mb-6">
                {plan.subtitle}
              </p>

              {/* Price */}
              <div className="text-center mb-8">
                <span className="text-4xl font-bold text-primary">{plan.price}</span>
                <span className="text-muted-foreground text-sm">{plan.period}</span>
              </div>

              {/* Features */}
              <ul className="space-y-3 mb-8 flex-1">
                {plan.features.map((feature, featureIndex) => (
                  <li key={featureIndex} className="flex items-start gap-3">
                    <Check className="w-5 h-5 text-primary shrink-0 mt-0.5" />
                    <span className="text-sm text-muted-foreground">{feature}</span>
                  </li>
                ))}
              </ul>

              {/* Button */}
              <button className={`w-full py-3 rounded-full font-medium transition-all duration-300 ${
                plan.popular 
                  ? 'bg-primary text-primary-foreground hover:bg-primary/90' 
                  : 'bg-transparent border-2 border-border text-foreground hover:border-primary hover:text-primary'
              }`}>
                Tham Gia Ngay
              </button>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default PricingSection;
