import { Heart, Infinity } from "lucide-react";
import FloatingHearts from "./FloatingHearts";

const HeroSection = () => {
  return (
    <section className="relative min-h-screen flex flex-col items-center justify-center px-4 py-20 overflow-hidden">
      <FloatingHearts />
      
      {/* Badge */}
      <div className="inline-flex items-center gap-2 bg-card px-4 py-2 rounded-full shadow-sm mb-8">
        <Heart className="w-4 h-4 text-primary fill-primary" />
        <span className="text-sm text-foreground">Chnh thc ra mt t 06/01/2026</span>
      </div>

      {/* Main Heading */}
      <h1 className="heading-display text-5xl md:text-7xl lg:text-8xl text-center mb-6">
        <span className="text-primary">D n </span>
        <span className="text-accent relative">
          Nui Chng
          <svg 
            className="absolute -bottom-2 left-0 w-full" 
            viewBox="0 0 200 12" 
            fill="none"
          >
            <path 
              d="M2 8C50 2 150 2 198 8" 
              stroke="hsl(var(--orange))" 
              strokeWidth="3" 
              strokeLinecap="round"
            />
          </svg>
        </span>
      </h1>

      {/* Subtitle */}
      <p className="text-center text-muted-foreground text-lg md:text-xl max-w-2xl mb-10 leading-relaxed">
        Ch c V mi c nui Chng! Minh bch ti chnh, yu thng v iu kin, hnh phc trn vn.
      </p>

      {/* CTA Button */}
      <button className="btn-rose text-lg">
        <Heart className="w-5 h-5 fill-current" />
        Tham Gia Ngay
      </button>

      {/* Stats */}
      <div className="grid grid-cols-3 gap-8 md:gap-16 mt-16 md:mt-24">
        <div className="text-center">
          <div className="text-4xl md:text-5xl font-bold text-primary mb-2">100%</div>
          <div className="text-sm text-muted-foreground">Minh Bch</div>
        </div>
        
        <div className="text-center">
          <div className="flex flex-col items-center mb-2">
            <Infinity className="w-10 h-10 md:w-12 md:h-12 text-primary" />
          </div>
          <div className="text-sm text-muted-foreground">Yu Thng</div>
        </div>
        
        <div className="text-center">
          <div className="text-4xl md:text-5xl font-bold text-primary mb-2">24/7</div>
          <div className="text-sm text-muted-foreground">Hnh Phc</div>
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
